<template>
	<view class="content">
		<cu-custom bgColor="theme-bg-color" :isBack="true">
			<block slot="backText"><text>返回</text></block>
			<block slot="content"><text>自习室</text></block>
		</cu-custom>
		<view class="center" v-if="detail!=null">
			<view class="title">{{detail.name}}</view>
			<view class="margin-top">
				<swiper class="screen-swiper" :class="dotStyle?'square-dot':'round-dot'" :indicator-dots="true" :circular="true"
					:autoplay="true" interval="5000" duration="500">
					<swiper-item >
						<image :src="detail.imgUrl|pictureFilter" mode="aspectFill"></image>
					</swiper-item>
				</swiper>
			</view>
			<view class="flex  justify-between padding-tb-sm">
				<view class=" margin-xs radius">
					<view class='cu-tag' style='margin-top:10upx' :class="'bg-orange'">最大容量:{{detail.capacity}}
					</view>
					
				</view>

			</view>
		    <view style="text-align: center;font-weight: bold;">座位明细</view>
			
			<view class="seat-list">
			<template v-for="(row,index) in detail.roomSeatList">
				
				<view class="row">
						<template v-for="(col,colIndex) in row">
							<view class="col" @click="ToDetail(col)">{{col.name}}</view>
						</template>
				</view>
				
			</template>
			</view>
			
		</view>

	</view>
</template>

<script>
	import uParse from '@/components/u-parse/u-parse.vue'
	import countdownTimer from '@/components/countdown-timer/countdown-timer.vue'
	export default {
		components: {
			uParse,
			'countdown-timer': countdownTimer
		},
		data() {
			return {
				detail: null,
				recordId: "",
				id: '',
				time: 0,
				timeInterval: 0,
				uploadInterval: 0,
			}
		},
		onLoad(option) {
			this.id = option.id;
			this.getDetailApi();


		},
		onUnload() {

		},
		methods: {

			async getDetailApi() {
				let url = "/room/detail?id=" + this.id;
				let {
					success,
					data
				} = await this.$http.PostAsync(url, {
					id: this.id
				});
				console.log(data);
				if (success == true) {
					this.detail = data;
				}
			},
		
			goBack() {
				uni.navigateBack();
			
			},
		
			async ToDetail(item){
				  console.log(JSON.stringify(item));
				  console.log(item.id);
				 uni.setStorageSync(item.id+"",JSON.stringify(item));
				uni.navigateTo({
					url: `/pages/room/room-apply?id=`+item.id+"&roomName="+this.detail.name+"&roomId="+this.detail.id
				})
			},
			preview(src, e) {
				// do something
			},
			navigate(href, e) {
				// do something
			}
		}
	}
</script>

<style scoped lang="scss">
	@import url("@/components/u-parse/u-parse.css");

	.content {
		background-color: white;
	}

	.center {
		padding: 20px;

	}

	.title {
		font-size: 40rpx;

	}

	.author {
		text-align: right;
		padding-bottom: 10rpx;
	}


	.publish {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		background-color: #ffaa00;
		width: 120upx;
		height: 120upx;
		border-radius: 50%;
		position: fixed;
		right: 30upx;
		bottom: 150upx;
		color:white;
	}
	.seat-list{
		margin-top: 20upx;
		.row{
			display: flex;
			flex-wrap: nowrap;
			font-size: 15upx;
		
		.col{
			padding:15upx;
			border:1upx solid #ffaa00 ;
		}
		}
	}
	
</style>
